              },
              value: {
                show: true,
                fontSize: "16px",
                fontFamily: "Helvetica, Arial, sans-serif",
                fontWeight: 400,
                color: undefined,
                offsetY: 16,
                formatter: function (val) {
                  return val;
                },
              },
            },
          },
        },

        chart: {
          toolbar: {
            show: true,
            offsetX: 0,
            offsetY: -34,
            tools: {
              download: true,
              selection: true,
              zoom: true,
              zoomin: true,
              zoomout: true,
              pan: true,
              reset: true | '<img src="/static/icons/reset.png" width="20">',
              customIcons: [],
            },
            export: {
              csv: {
                filename: "Export Chart",
                columnDelimiter: ",",
                headerCategory: "category",
                headerValue: "value",
                dateFormatter(timestamp) {
                  return new Date(timestamp).toDateString();
                },
              },
              svg: {
                filename: "Export Chart",
              },
              png: {
                filename: "Export Chart",
              },
            },
            autoSelected: "zoom",
          },
        },

        legend: {
          show: false,
        },
      },
      series: [],

      // table
      perPage: 5,
      pageOptions: [3, 5, 10],
      totalRows: 1,
      currentPage: 1,
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      fields: [
        {
          key: "hrcode",
          label: "HR Code",
        },
        {
          key: "profile_photo_url",
          label: "Avatar",
        },
        { key: "name", label: "Full Name", sortable: true },
        "email",
        { key: "department.name", label: "Department", sortable: "true" },
        { key: "team.name", label: "Position", sortable: "true" },
        { key: "is_active", label: "Status", sortable: true },
      ],
      items: [],
      status: [
        {
          1: "Active",
          2: "Inactive",
        },
        {
          1: "light-success",
          2: "light-danger",
        },
      ],

      // export excel
      exportItems: [],
      exportFields: {
        "HR Code": "hrcode",
        Name: "name",
        Email: "email",
        Department: "department.name",
        Position: "team.name",
        Status: "is_active",
      },
    };
  },

  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => ({ text: f.label, value: f.key }));
    },
  },
  mounted() {
    // Set the initial number of items
    this.totalRows = this.items.length;

    // fetch data
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.callApi({
        method: "GET",
        url: "/users/getAll",
        success: (response) => {
          this.totalRows = this.items.length;
          this.items = response.result.data;

          // charts
          // let chartData = [{ name: "Age", data: [22, 33] }];
          response.result.data.forEach((item) => {
            this.series.push(item.age);
          });

          // this.series = chartData;

          // console.log(this.series);

          // export excel
          this.exportItems = response.result.data;
        },
        error: (error) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Failed to fetch data",
              variant: "error",
            },
          });
        },
      });
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
  },
};
</script>
